/**
 * @name isSelected
 *
 * @description
 * Helper to know if the batch is selected.
 *
 * @param {string} id
 * @param {object} selState
 * @type {function}
 * @returns {boolean}
 */
export const isSelected = (id, selState) =>
  selState.selectedBatches.some(item => item === id);

/**
 * @name isAnySelected
 *
 * @description
 * Helper to know if any batch is selected.
 *
 * @param {object} selState
 * @type {function}
 * @returns {boolean}
 */
export const isAnySelected = selState =>
  selState.selectedBatches.length > 0;

/**
 * @name howManySelected
 *
 * @description
 * Helper to know how many payments are selected.
 *
 * @param {object} selState
 * @type {function}
 * @returns {boolean}
 */
export const howManySelected = selState =>
  selState.selectedBatches.length;

/**
 * @name isSelectedAllStrict
 * @inner
 *
 * @description
 * Strictly compare selected items ids and model list
 *
 * @param {array} batches
 * @param {object} selState
 * @type {function}
 * @returns {boolean}
 */
const isSelectedAllStrict = (batches, selState) => {
  const selected = selState.selectedBatches;

  if (batches.length !== selected.length) {
    return false;
  }

  const matches = batches.filter(item => selected.indexOf(item.id) !== -1);
  return matches.length === batches.length;
};

/**
 * @name isSelectedAll
 *
 * @description
 * Helper to know if all bacthes are selected.
 *
 * @param {array} batches
 * @param {object} selState
 * @type {function}
 * @returns {boolean}
 */
export const isSelectedAll = (batches, selState) =>
  batches && batches.length > 0 && isSelectedAllStrict(batches, selState);

/**
 * @name isSelectedPart
 *
 * @description
 * Helper to know if at least one batch is selected.
 *
 * @param {array} batches
 * @param {object} selState
 * @type {function}
 * @returns {boolean}
 */
export const isSelectedPart = (batches, selState) =>
  isAnySelected(selState) && !isSelectedAll(batches, selState);

/**
 * @name toggleAllBatches
 *
 * @description
 * Helper to select/deselect all batches loaded.
 *
 * @param {array} batches
 * @param {object} selState
 * @type {function}
 * @returns {null}
 */
export const toggleAllBatches = (bacthes, selState) => {
  const state = selState;
  if (isSelectedAll(bacthes, selState)) {
    state.selectedBatches = [];
  } else {
    state.selectedBatches = bacthes.map(item => item.id);
  }
};

/**
 * @name deselectBatches
 *
 * @description
 * Helper to deselect all batches selected.
 *
 * @param {object} selState
 * @type {function}
 * @returns {null}
 */
export const deselectBatches = selState => {
  const state = selState;
  state.selectedBatches = [];
};

/**
 * @name toggleSelection
 *
 * @description
 * Helper to select/deselect single batch.
 *
 * @param {string} id
 * @param {object} selState
 * @type {function}
 * @returns {null}
 */
export const toggleSelection = (id, selState) => {
  const state = selState;

  if (isSelected(id, state)) {
    state.selectedBatches = state.selectedBatches.filter(item => item !== id);
  } else {
    state.selectedBatches.push(id);
  }
};


